@import "./../../vars.scss";

.project-card {
  transition: transform 0.2s ease-in;
  cursor: pointer;
  position: relative;
  text-align: center;
  width: 25%;
  &__head {
    color: $white;
    padding: 30px;
    border: 1px solid #404040;
    border-bottom: none;
    transition: background-color 0.2s ease-in;
    height: 120px;
    @media screen and (max-width: 1388px) {
      padding: 15px;
      padding-top: 25px;
    }
    @media screen and (max-width: 1268px) {
      padding: 10px;
      padding-top: 15px;
    }
  }

  &__name {
    font-size: 18px;
    font-family: "Proxima Nova Bold";
    line-height: 1.2;
    margin-bottom: 5px;
    // white-space: nowrap;
  }

  &__description {
    font-size: 14px;
    line-height: 150%;
    // white-space: nowrap;
    color: $grey;
    transition: color 0.2s ease-in;
  }

  &__image {
    height: 203px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &:hover .project-card__head {
    background-color: $pink;
  }
  &:hover .project-card__description {
    color: $white;
  }
  &:hover {
    transform: scale(1.05);
  }
